.skills {
  min-height: 865px;
  padding: 113px 0 68px;
  color: var(--primary-white-color);
  background-image: linear-gradient(
    90deg,
    var(--secondary-black-color) 0%,
    var(--primary-gray-color) 100%
  );
}

.skills-light {
  color: var(--primary-black-color);
  background-image: linear-gradient(
    90deg,
    var(--third-white-color) 0%,
    var(--secondary-white-color) 100%
  );
}

.title {
  text-align: center;
  text-transform: uppercase;
  font-size: 48px;
  font-weight: 400;
  line-height: 1.32;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 40px;
  row-gap: 30px;
  min-height: 570px;
  margin-top: 50px;
}

.item {
  width: 270px;
  height: 270px;
}
